<template>
  <div class="search-page">
    <van-search
      :placeholder="$store.state.hotWords[$store.state.hotWordsIndex]"
      shape="round"
      left-icon=""
      :clearable="false"
      show-action
      @search="onSearch"
      v-model="searchText"
      @focus="showsuggest = true"
    >
      <div slot="right-icon" v-if="searchText">
        <van-icon color="lightgray" name="clear" @click="searchText = ''" />
      </div>
      <div class="actionbtn" slot="action" @click="onCancel">取消</div>
    </van-search>
    <!-- 推荐 -->
    <div class="search-recommend" v-if="!searchText">
      <!-- 搜索热词 -->
      <div class="hotsearchword">
        <van-cell to="searchhotwordspage">
          <!-- 使用 title 插槽来自定义标题 -->
          <template slot="title">
            <span class="custom-title">搜索热词</span>
          </template>
          <template slot="default">
            <span class="custom-title-right">查看更多 </span>
          </template>
          <van-icon name="arrow" />
        </van-cell>
        <div class="hotsearchword-content">
          <van-tag
            size="medium"
            plain
            round
            v-for="(item, index) in searchHotWords"
            :key="index"
            @click="getBooks(item.word)"
            >{{ item.word }}</van-tag
          >
        </div>
      </div>
      <div class="hotrecomment">
        <!-- 热门推荐 -->
        <van-cell>
          <template slot="title">
            <span class="custom-title">热门推荐</span>
          </template>
          <template slot="default">
            <span class="custom-title-right" @click="changeHotWords">换一批 </span>
          </template>
          <van-icon name="replay" />
        </van-cell>
        <!-- Layout 布局 -->
        <van-row>
          <van-col
            span="12"
            v-for="(item, index) in hotWords"
            :key="index"
            @click="toBookDetail(item.book)"
          >
            <van-icon name="orders-o" />
            <span>{{ item.word }}</span>
          </van-col>
        </van-row>
      </div>
    </div>
    <!-- 搜索建议 -->
    <div class="search-suggest" v-if="searchText && showsuggest">
      <div class="scroll-list-wrap">
        <cube-scroll ref="scroll">
          <van-list>
            <van-cell v-for="(item, index) in suggest" :key="index" @click="getBookId(item)">
              <template slots="default">
                <van-icon name="orders-o" />
                <span>{{ item }}</span>
              </template>
            </van-cell>
          </van-list>
        </cube-scroll>
      </div>
    </div>
    <!-- 搜索回车后 -->
    <div class="search-detail" v-if="searchText && !showsuggest">
      <!-- tab标签页 -->
      <van-tabs
        line-width="15px"
        line-height="2px"
        title-active-color="#db2016"
        v-model="active"
        animated
      >
        <!-- 每一项tab标签 -->
        <van-tab title="书籍">
          <div class="scroll-list-wrap">
            <cube-scroll ref="scroll">
              <van-list v-model="loading" :finished="finished" finished-text="到底了">
                <van-cell
                  class="search-detail-list"
                  v-for="(item, index) in books"
                  :key="index"
                  @click="toBookDetail(item._id)"
                >
                  <!-- layout布局 左边6图片 右边18详情 -->
                  <van-row type="flex">
                    <van-col span="6" class="search-detail-list-img">
                      <van-image width="75" height="100" :src="item.cover" />
                    </van-col>
                    <van-col span="18" class="search-detail-list-info">
                      <div class="title">{{ item.title }}</div>
                      <div class="shortIntro">{{ item.shortIntro }}</div>
                      <div class="bottom">
                        <span class="author">{{ item.author }}</span>
                        <van-tag class="tag" plain>{{ item.cat }}</van-tag>
                      </div>
                    </van-col>
                  </van-row>
                </van-cell>
              </van-list>
            </cube-scroll>
          </div>
        </van-tab>
        <van-tab title="漫画">内容 2</van-tab>
        <van-tab title="书单">内容 3</van-tab>
        <van-tab title="社区">内容 4</van-tab>
      </van-tabs>
      <!-- 下拉用定位
      <van-dropdown-menu v-if="active == 0">
        <van-dropdown-item v-model="value1" :options="option1" />
        <van-dropdown-item v-model="value2" :options="option2" />
      </van-dropdown-menu> -->
    </div>
    <!-- <van-loading size="24px">加载中...</van-loading> -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchText: "",
      searchHotWords: null,
      hotWords: null,
      active: 0,
      value1: 0,
      value2: "a",
      option1: [
        { text: "综合", value: 0 },
        { text: "按人气", value: 1 },
        { text: "按留存", value: 2 },
        { text: "按评分", value: 3 },
        { text: "按字数", value: 4 }
      ],
      option2: [
        { text: "默认排序", value: "a" },
        { text: "好评排序", value: "b" },
        { text: "销量排序", value: "c" }
      ],

      books: null,
      suggest: null,
      showsuggest: false,
      list: [],
      loading: false,
      finished: false,
      scroll: null
    };
  },
  watch: {
    books() {
      if (this.books) {
        this.books.forEach(e => {
          //slice(7)过滤掉/agent/   unescape对图片进行解码
          e.cover = unescape(e.cover.slice(7));
        });
      }
    },
    searchText() {
      if (this.searchText) {
        this.axios
          .get(this.$origin + "/auto-complete", {
            params: {
              query: this.searchText
            }
          })
          .then(res => {
            window.console.log(res);
            this.suggest = res.data.keywords;
          });
        //每一次拿数据前清空上一次的数据
        // this.books = null;
        // this.axios
        //   .get(this.$origin + "/search", {
        //     params: {
        //       keyword: this.searchText
        //     }
        //   })
        //   .then(res => {
        //     window.console.log(res);
        //     this.books = res.data.books;
        //   });
      }
    }
  },
  created() {
    window.console.log(this.$route);
    if (this.$route.query.searchText) {
      this.searchText = this.$route.query.searchText;
    }
    //取八个热搜词
    this.searchHotWords = this.$store.state.searchHotWords.slice(0, 8);
    // 取六个热门推荐词
    this.changeHotWords();
    // this.$nextTick(() => {
    //   window.console.log(this.$el.querySelector(".van-search input"));
    //   this.$el.querySelector(".van-search input").focus();
    // });
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      // 通过 `vm` 访问组件实例
      window.console.log(from);
      //如果是从详情页退回来不用聚焦
      if (from.path !== "/bookdetail") {
        vm.$el.querySelector(".van-search input").focus();
      }
      if (from.path == "/searchhotwordspage") {
        vm.$el.querySelector(".van-search input").blur();
        vm.getBooks(vm.$route.query.searchText);
      }
    });
  },
  methods: {
    getBookId(bookName) {
      this.axios
        .get(this.$origin+"/search", {
          params: {
            keyword: bookName
          }
        })
        .then(res => {
          this.toBookDetail(res.data.books[0]._id);
        });
    },
    toBookDetail(id) {
      this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true,
        message: "加载中..."
      });
      this.$router.push({ path: "bookdetail", query: { bookId: id } });
    },
    getBooks(txt = "") {
      if (txt) {
        this.searchText = txt;
      }
      window.console.log("回车");
      this.showsuggest = false;
      if (this.searchText) {
        this.books = null;
        // this.$toast.loading({
        //   duration: 0, // 持续展示 toast
        //   forbidClick: true,
        //   message: "加载中..."
        // });
        this.finished = false;
        this.loading = true;
        this.axios
          .get(this.$origin + "/search", {
            params: {
              keyword: this.searchText
            }
          })
          .then(res => {
            window.console.log(res);
            this.books = res.data.books;
            // 加载状态结束
            this.loading = false;
            if (this.loading == false) {
              // 数据全部加载完成
              this.finished = true;
            }
            // this.$toast.clear()
          });
      }
    },
    onSearch() {
      //onsearch在回车触发
      window.console.log("onSearch");
      this.getBooks();
    },
    onCancel() {
      //onsearch在点击取消触发
      window.console.log("onCancel");
      if (this.$route.query.searchText) {
        this.$router.go(-2);
      } else {
        this.$router.go(-1);
      }
      this.searchText = "";
    },
    changeHotWords() {
      //每次截取6条
      //然后把截取后的6条显示并且push到数组中

      //另一个思路，每次显示前6条，每次点击换一批就截取前面的6条放到后面
      this.hotWords = this.$store.state.newHotWords.slice(0, 6);
      this.$store.commit("changeHotWords");
    }
  }
};
</script>

<style lang="scss">
@mixin oneline() {
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.search-page {
  //搜索框
  .van-search {
    border-bottom: 1px solid #e6e4e4;
    position: sticky;
    top: 0;
    z-index: 99;
    .actionbtn {
      color: #db2016;
      font-weight: bold;
    }
  }
  //推荐
  .search-recommend {
    .hotsearchword {
      .van-cell {
        .custom-title {
          color: #484848;
        }
        .custom-title-right {
          color: #a7a7a7;
          font-size: 12px;
        }
        .van-icon {
          vertical-align: middle;
        }
      }
      .hotsearchword-content {
        padding: 0 16px 10px;
        //搜索热词tag
        .van-tag {
          margin: 5px;
          color: #606060;
          border-color: #e6e4e4;
        }
      }
    }
    .hotrecomment {
      .van-cell {
        .custom-title {
          color: #484848;
        }
        .custom-title-right {
          color: #a7a7a7;
          font-size: 12px;
        }
        .van-icon {
            vertical-align: middle;
          }
      }
      //热门推荐下的列表
      .van-row {
        padding: 0 10px 10px;
        .van-col {
          @include oneline();
          padding-right: 0;
          height: 24px;
          line-height: 24px;
          margin: 3px 0; 
          .van-icon {
            vertical-align: middle;
          }
          span {
            margin-left: 5px;
            font-size: 14px;
            color: #646566;
          }
        }
      }
    }
  }
  //搜索建议
  .search-suggest {
    // .scroll-list-wrap {
    //   height: 100px;
    //   .cube-scroll-wrapper {
    //     overflow: visible;
    //     .cube-scroll-list-wrapper {
    //       overflow: visible;
    //     }
    //   }
    // }
    .van-list {
      .van-cell {
        .van-icon {
          vertical-align: middle;
        }
        span {
        }
      }
    }
  }

  .search-detail {
    //better-scroll必须给容器一个高度
    .van-tabs__content.van-tabs__content--animated {
      overflow: visible;
    }
    .scroll-list-wrap {
      height: 400px;
      .cube-scroll-wrapper {
        overflow: visible;
        .cube-scroll-list-wrapper {
          overflow: visible;
        }
      }
    }
    .van-tabs {
      .van-tabs__wrap.van-hairline--top-bottom {
        position: sticky;
        top: 55px;
        z-index: 99;
      }
    }
    .search-detail-list {
      padding: 0 10px;
      .van-row {
        // height: 150px;
        padding: 10px 0;
        .search-detail-list-img {
          text-align: center;
          .van-image {
            box-shadow: 4px 5px 6px -2px rgba(0, 0, 0, 0.4);
          }
        }
        .search-detail-list-info {
          margin-left: 10px;
        }
        .van-col {
          height: 100px;
          .title {
          }
          .shortIntro {
            font-size: 12px;
            height: 48px;
            @include oneline();
            -webkit-line-clamp: 2;
          }
          .bottom {
            height: 28px;
            font-size: 12px;
            .author {
              margin: 0;
            }
            .tag {
              margin: 0;
              float: right;
              font-size: 12px;
              margin-top: 2px;
            }
          }
        }
      }
    }
  }
}
</style>
